
.DatePlan .headerPart {
  padding: 4rem 0;
  color: red;
  text-align: center;
}
.DatePlan .headerPart .myTitle {
    color: #3cc51f;
    font-size: 34px;
    font-weight: 400;
}
.DatePlan .headerPart .info {
    color: gray;
    font-size: 1.7rem;
}
.DatePlan .headerPart .textPart {
    border: none;
    border-bottom: 1px solid gray;
    height: 40px;
    line-height: 40px;
    margin: 0 10px 5px 0;
    outline: none;
    width: 80%;
    text-align: center;
}
.DatePlan .add_icon {
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  line-height: 1.25rem;
  border-radius: 50%;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
  color: white;
  font-size: 2rem;
  background: #3cc51f;
}
.DatePlan .doneTag {
  text-decoration: line-through;
}
.DatePlan .weui-cell__bd input {
  width: 89%;
}
.DatePlan .weui-cell__bd span {
  display: inline-block;
  width: 89%;
}
.DatePlan .weui-cell {
  -webkit-transition: 250ms ease-out;
  transition: 250ms ease-out;
  -webkit-transform-origin: center;
          transform-origin: center;
  -webkit-transform: scalex(1);
          transform: scalex(1);
  -webkit-transform: translate(100%, 1);
          transform: translate(100%, 1);
  opacity: 1;
}
.DatePlan .fixBottom {
  position: fixed;
  bottom: 0px;
  width: 100%;
}
.DatePlan .showDialog {
  opacity: 1 !important;
  display: block !important;
}
.DatePlan .hideDialog {
  opacity: 0;
  display: none;
}
.DatePlan .showDetail {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}



